* {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

body {
  background-color: #ffe9d6;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #654062;
  font-family: 'Noto Sans JP', sans-serif;
}

.wrapper-grid {
  display: grid;
  grid-template-columns: 1fr 4fr;
}

#component-root {
  padding: 0.5rem;
  margin-right: 1rem;
}

#nav {
  margin: 0 2rem;
  border-radius: 2rem;
  padding: 0.5rem;
  background-color: #f9f3ed;
  text-align: center;
  height: 50rem;
}

#footer {
  text-align: center;
}

.wrapper {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
}

.profile-img {
  width: 70%;
  height: auto;
  border-radius: 50%;
  margin-top: 5rem;
}

.menu-link {
  text-decoration: none;
  font-family: 'JetBrains Mono', monospace;
  font-size: 1.25em;
  padding: 0.25rem;
  margin: 1rem;
  transition: all 150ms ease-out;
}

.menu-link:hover {
  text-decoration: none;
  font-family: 'JetBrains Mono', monospace;
  font-weight: bold;
  font-size: 1.75em;
  padding: 0.25rem;
  margin: 0.05rem;
}

.menu-link:active {
  color: #86aba1;
}

